
[layout]{display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:stretch;-ms-flex-align:stretch;align-items:stretch
}
[layout]>*{-ms-flex-negative:0;-webkit-flex-shrink:0;flex-shrink:0
}
[layout~=column]{-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column
}
[layout~=row]{-webkit-flex-direction:row;-ms-flex-direction:row;flex-direction:row
}
[layout~=center]{-webkit-align-items:center;-ms-flex-align:center;align-items:center
}
[layout~=u05][layout~=row]>*+*{margin-left:4px
}
[layout~=u05][layout~=column]>*+*{margin-top:4px
}
[layout~=u1][layout~=row]>*+*{margin-left:8px
}
[layout~=u1][layout~=column]>*+*{margin-top:8px
}
[layout~=u2][layout~=row]>*+*{margin-left:16px
}
[layout~=u2][layout~=column]>*+*{margin-top:16px
}
[layout~=u3][layout~=row]>*+*{margin-left:24px
}
[layout~=u3][layout~=column]>*+*{margin-top:24px
}
[layout~=u4][layout~=row]>*+*{margin-left:32px
}
[layout~=u4][layout~=column]>*+*{margin-top:32px
}
[flex]{-webkit-flex:1;-ms-flex:1;flex:1
}
html,body{margin:0;width:100%;height:100%;font-size:9px;font-family:Arial, sans-serif;background:black;overflow:hidden
}



